.techs {
  padding: 100px 70px;
  background: #272727;
}

.techs__title {
  margin-bottom: 90px;
}

.techs__subtitle {
  line-height: 58px;
  letter-spacing: -.04em;
  margin: 0 0 26px;
  font-weight: 400;
  font-size: 50px;  
}

.techs__txt {
  max-width: 460px;
  text-align: center;
  margin: 0 0 100px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -.04em;  
}

.techs__list {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(auto-fit, 90px);
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;  
}

@media screen and (max-width: 1024px) {
  .techs {
    padding: 90px 50px;
  }

  .techs__list {
    grid-template-columns: repeat(auto-fit, 84px);
  }
}

@media screen and (max-width: 768px) {
  .techs__title {
    margin-bottom: 80px;
  }

  .techs__subtitle {
    margin-bottom: 22px;
  }

  .techs__txt {
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 83px;
  }
}

@media screen and (max-width: 620px) {
  .techs {
    padding: 70px 18px;
  }


  .techs__title {
    margin-bottom: 60px;
  }

  .techs__txt {
    font-size: 11px;
    line-height: 16px;
    margin-bottom: 50px;
  }

  .techs__subtitle {
    font-size: 30px;
    line-height: 36px;
    margin-bottom: 24px;
  }

  .techs__list {
    width: 90%;
  }
}
